body {
  font-size: 36px;
  margin: 0 auto;
  padding: 0;
  max-width: 80%;
}

* {
  margin: 0;
  padding: 0;
  vertical-align: middle;
}

body {
  margin: 0 auto;
  max-width: 90%;
  font-family: "open sans", sans-serif;
  font-size: 14px;
  min-height: 100vh;
  display: flex;
}

#root,
#main,
.app {
  display: flex;
  width: 100%;
}

#main {
  flex-grow: 1;
  margin: 30px 0;
}

.app {
  flex-flow: column;
}

aside {
  min-width: 20vw;
  margin: 0 30px 0 0;
}

footer {
  padding: 10px 20px;
  color: #fff;
  background-color: #000;
}

header div {
  position: relative;
  height: 30vh;
}

header img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center top;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

aside > nav {
}
aside > nav > ul {
  list-style-type: none;
}
aside > nav > ul > li {
}
aside > nav > ul > li > a {
  display: block;
  color: #000;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 700;
  padding: 8px 0;
  transition: 0.2s;
}

aside > nav > ul > li > a:hover {
  transform: translateX(20px);
}

aside > nav > ul > li > a.active {
  padding-left: 10px;
  border-left: 4px solid black;
}

.articleListItem {
  margin-top: 30px; }
  .articleListItem h3 {
    font-size: 75%; }
  .articleListItem p {
    margin-top: 15px; }

